header {
  background-image: url(".././images/header/header-phone.jpg");
  width: 100vw;
  height: 100%;
  padding: 40px 0;
}

.header_svg {
  width: 106.99px;
  height: 90px;
}

.header-title {
  text-transform: uppercase;
  font-family: var(--font-family);
  font-weight: 900;
  font-size: 18px;
  text-align: center;
  margin: 0 auto; 
  color: #dc56c5;
}
.search-bar {
  position: relative;
}
.search-bar__input {
  border-radius: 20px;
  width: 270px;
  height: 40px;
  padding: 10px 118px 10px 30px;
  backdrop-filter: blur(20px);
  background-color: rgba(255, 255, 255, 0.2);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  color: black;
}
.choose-country__select {
  border-radius: 20px;
  width: 270px;
  height: 40px;
  padding: 10px 118px 10px 30px;
  backdrop-filter: blur(20px);
  background-color: rgba(255, 255, 255, 0.2);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  color: #939393;
}
.choose-country__option {
  border-radius: 20px;
  backdrop-filter: blur(20px);
  background-color: rgba(255, 255, 255, 0.2);
  color: #939393;
}

.search-bar {
  margin: 25px 25px 25px 25px;
}
.choose-country {
  margin-left: 25px;
}

.search-bar__svg {
  width: 12px;
  height: 12px;
  position: absolute;
  right: 30px;
  top: 15px;
}
.container-div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.dropdown {
  position: relative;
  margin-bottom: 30px;

  @media screen and (min-width: 768px) {
    margin-bottom: 0px;
  }
}

.choose_btn {
  width: 270px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  padding: 10px 113px 10px 30px;
  color: #939393;
  font-size: 16px;
  cursor: pointer;
}
.choose-bar__svg {
  width: 15px;
  height: 10px;
  top: 15px;
  right: 30px;
  position: absolute;
}
.dropdown-list {
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.2);
  width: 270px;
  backdrop-filter: blur(10px);
  padding: 10px 0;
  height: 150px;
  overflow: auto;
  display: none;
}
.dropdown_item {
  width: 100%;
  border-bottom: black solid 1px;
  padding: 10px 20px;
  color: #0e0e0e;
  font-size: 16px;
  cursor: pointer;
}
.active {
  display: block;
}
@media (min-width: 768px) {
  header {
    background-image: url(".././images/header/header-tablet.jpg");
    width: 100vw;
    background-size: cover;
background-position: center;

  }
  .header-up {
    height: 100px;
  }
  .header_svg {
    width: 180px;
    height: 151px;
    position: absolute;
    top: 0;
    left: 0;
  }

  .header-title {
    // position: absolute;
    // left: 208px;
    // top: 40px;
    font-size: 30px;

    // margin-left: 208px;
    // margin: right;
    // margin-top: 40px;
    //    width: 353px;
  }

  .search-bar {
    width: 270px;
    height: 40px;
    margin-left: 99px;
    margin-right: 30px;
    margin-top: 27px;
  }
  .dropdown {
    width: 270px;
    height: 40px;
  }

  .container-div {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
  }
}
@media (min-width: 1280px) {
  header {
    background-image: url(".././images/header/header-computer.jpg");
    width: 100vw;
    height: 446px;
    background-size: cover;
  }
  .header_svg {
    width: 235px;
    height: 207px;
    position: absolute;
    top: 0;
    left: 0;
  }
  .header-title {
    font-weight: 900;
    font-size: 50px;

  }
  .container-div {
    margin-top: 104px;
    justify-content: center;
    display: flex;
    align-items: center;
  }
  .search-bar {
    margin: 0;
    margin-right: 30px;
  }
  .choose-country {
    // margin: 0;
  }
}
